/* styles.css */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  
}

header {
  background-color: #000000;
  padding: 10px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: white;
  position: sticky;
  top: 0;
  z-index: 1000;
}

header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

header .logo h1 {
  margin: 0;
}

header .nav {
  display: flex;
  list-style: none;
}

header .nav ul {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
}

header .nav li {
  margin: 0 10px;
}

header .nav a {
  color: #1D9AF2;
  background-color: #000000;
 
  padding: 0 15px;
  height: 32px;
  font-size: 14px;
  text-decoration: none;
  display: flex;
  align-items: center;
  transition: all 0.2s ease-in-out;
}

header .nav a:hover {
  box-shadow: 1px 1px #53a7ea, 2px 2px #53a7ea, 3px 3px #53a7ea;
  transform: translateX(-3px);
}

header .hamburger {
  display: none;
  flex-direction: column;
  cursor: pointer;
}

header .bar {
  height: 3px;
  width: 25px;
  background-color: white;
  margin: 4px 0;
  transition: all 0.3s ease;
}

@media (max-width: 768px) {
  header .nav {
      display: none;
      flex-direction: column;
      width: 100%;
      background-color: #000000;
      position: absolute;
      top: 60px;
      left: 0;
  }

  header .nav.open {
      display: flex;
  }

  header .nav ul {
      flex-direction: column;
      width: 100%;
  }

  header .nav li {
      margin: 10px 0;
      text-align: center;
  }

  header .hamburger {
      display: flex;
  }
}

main {
  padding: 0px;
  
}

section {
  padding: 29px 0 ;
  background-image: linear-gradient(to right, #eac5f2, #ebd1f7, #eddcfa, #f1e7fd, #f6f2fe, #f9f6ff, #fcfbff, #ffffff, #ffffff, #ffffff, #ffffff, #ffffff);
  


}

h1, h2 {
  color: #101111;
  text-align: center;
}

#home {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content:left;
  background: url("../src/assets/cara.jpeg")no-repeat center center/cover;;
  color: rgb(0, 0, 0);
  text-align: left;
  
  

}
